.is-focused\n        border-color: transparent\n        color: $color-invert\n        &:not(:active)\n          box-shadow: $button-focus-box-shadow-size bulmaRgba($color, 0.25)\n      &:active,\n      &.is-active\n        background-color: bulmaDarken($color, 5%)\n        border-color: transparent\n        color: $color-invert\n      &[disabled],\n      fieldset[disabled] &\n        background-color: $color\n        border-color: $color\n        box-shadow: none\n      &.is-inverted\n        background-color: $color-invert\n        color: $color\n        &:hover,\n        &.is-hovered\n          background-color: bulmaDarken($color-invert, 5%)\n        &[disabled],\n        fieldset[disabled] &\n          background-color: $color-invert\n          border-color: transparent\n          box-shadow: none\n          color: $color\n      &.is-loading\n        &::after\n          border-color: transparent transparent $color-invert $color-invert !important\n      &.is-outlined\n        background-color: transparent\n        border-color: $color\n        color: $color\n        &:hover,\n        &.is-hovered,\n        &:focus,\n        &.is-focused\n          background-color: $color\n          border-color: $color\n          color: $color-invert\n        &.is-loading\n          &::after\n            border-color: transparent transparent $color $color !important\n          &:hover,\n          &.is-hovered,\n          &:focus,\n          &.is-focused\n            &::after\n              border-color: transparent transparent $color-invert $color-invert !important\n        &[disabled],\n        fieldset[disabled] &\n          background-color: transparent\n          border-color: $color\n          box-shadow: none\n          color: $color\n      &.is-inverted.is-outlined\n        background-color: transparent\n        border-color: $color-invert\n        color: $color-invert\n        &:hover,\n        &.is-hovered,\n        &:focus,\n        &.is-focused\n          background-color: $color-invert\n          color: $color\n        &.is-loading\n          &:hover,\n          &.is-hovered,\n          &:focus,\n          &.is-focused\n            &::after\n              border-color: transparent transparent $color $color !important\n        &[disabled],\n        fieldset[disabled] &\n          background-color: transparent\n          border-color: $color-invert\n          box-shadow: none\n          color: $color-invert\n      // If light and dark colors are provided\n      @if length($pair) >= 4\n        $color-light: nth($pair, 3)\n        $color-dark: nth($pair, 4)\n        &.is-light\n          background-color: $color-light\n          color: $color-dark\n          &:hover,\n          &.is-hovered\n            background-color: bulmaDarken($color-light, 2.5%)\n            border-color: transparent\n            color: $color-dark\n          &:active,\n          &.is-active\n            background-color: bulmaDarken($color-light, 5%)\n            border-color: transparent\n            color: $color-dark\n  // Sizes\n  &.is-small\n    +button-small\n  &.is-normal\n    +button-normal\n  &.is-medium\n    +button-medium\n  &.is-large\n    +button-large\n  // Modifiers\n  &[disabled],\n  fieldset[disabled] &\n    background-color: $button-disabled-background-color\n    border-color: $button-disabled-border-color\n    box-shadow: $button-disabled-shadow\n    opacity: $button-disabled-opacity\n  &.is-fullwidth\n    display: flex\n    width: 100%\n  &.is-loading\n    color: transparent !important\n    pointer-events: none\n    &::after\n      @extend %loader\n      +center(1em)\n      position: absolute !important\n  &.is-static\n    background-color: $button-static-background-color\n    border-color: $button-static-border-color\n    color: $button-static-color\n    box-shadow: none\n    pointer-events: none\n  &.is-rounded\n    border-radius: $radius-rounded\n    padding-left: calc(#{$button-padding-horizontal} + 0.25em)\n    padding-right: calc(#{$button-padding-horizontal} + 0.25em)\n\n.buttons\n  align-items: center\n  display: flex\n  flex-wrap: wrap\n  justify-content: flex-start\n  .button\n    margin-bottom: 0.5rem\n    &:not(:last-child):not(.is-fullwidth)\n      +ltr-property(\"margin\", 0.5rem)\n  &:last-child\n    margin-bottom: -0.5rem\n  &:not(:last-child)\n    margin-bottom: 1rem\n  // Sizes\n  &.are-small\n    .button:not(.is-normal):not(.is-medium):not(.is-large)\n      +button-small\n  &.are-medium\n    .button:not(.is-small):not(.is-normal):not(.is-large)\n      +button-medium\n  &.are-large\n    .button:not(.is-small):not(.is-normal):not(.is-medium)\n      +button-large\n  &.has-addons\n    .button\n      &:not(:first-child)\n        border-bottom-left-radius: 0\n        border-top-left-radius: 0\n      &:not(:last-child)\n        border-bottom-right-radius: 0\n        border-top-right-radius: 0\n        +ltr-property(\"margin\", -1px)\n      &:last-child\n        +ltr-property(\"margin\", 0)\n      &:hover,\n      &.is-hovered\n        z-index: 2\n      &:focus,\n      &.is-focused,\n      &:active,\n      &.is-active,\n      &.is-selected\n        z-index: 3\n        &:hover\n          z-index: 4\n      &.is-expanded\n        flex-grow: 1\n        flex-shrink: 1\n  &.is-centered\n    justify-content: center\n    &:not(.has-addons)\n      .button:not(.is-fullwidth)\n        margin-left: 0.25rem\n        margin-right: 0.25rem\n  &.is-right\n    justify-content: flex-end\n    &:not(.has-addons)\n      .button:not(.is-fullwidth)\n        margin-left: 0.25rem\n        margin-right: 0.25rem\n\n@each $bp-name, $bp-sizes in $button-responsive-sizes\n  +breakpoint($bp-name)\n    @each $size, $value in $bp-sizes\n      @if $size != \"normal\"\n        .button.is-responsive.is-#{$size}\n          font-size: $value\n      @else\n        .button.is-responsive,\n        .button.is-responsive.is-normal\n          font-size: $value\n","@import \"../utilities/mixins\"\n\n$container-offset: (2 * $gap) !default\n$container-max-width: $fullhd !default\n\n.container\n  flex-grow: 1\n  margin: 0 auto\n  position: relative\n  width: auto\n  &.is-fluid\n    max-width: none !important\n    padding-left: $gap\n    padding-right: $gap\n    width: 100%\n  +desktop\n    max-width: $desktop - $container-offset\n  +until-widescreen\n    &.is-widescreen:not(.is-max-desktop)\n      max-width: min($widescreen, $container-max-width) - $container-offset\n  +until-fullhd\n    &.is-fullhd:not(.is-max-desktop):not(.is-max-widescreen)\n      max-width: min($fullhd, $container-max-width) - $container-offset\n  +widescreen\n    &:not(.is-max-desktop)\n      max-width: min($widescreen, $container-max-width) - $container-offset\n  +fullhd\n    &:not(.is-max-desktop):not(.is-max-widescreen)\n      max-width: min($fullhd, $container-max-width) - $container-offset\n","@import \"../utilities/mixins\"\n\n$content-heading-color: $text-strong !default\n$content-heading-weight: $weight-semibold !default\n$content-heading-line-height: 1.125 !default\n\n$content-block-margin-bottom: 1em !default\n\n$content-blockquote-background-color: $background !default\n$content-blockquote-border-left: 5px solid $border !default\n$content-blockquote-padding: 1.25em 1.5em !default\n\n$content-pre-padding: 1.25em 1.5em !default\n\n$content-table-cell-border: 1px solid $border !default\n$content-table-cell-border-width: 0 0 1px !default\n$content-table-cell-padding: 0.5em 0.75em !default\n$content-table-cell-heading-color: $text-strong !default\n$content-table-head-cell-border-width: 0 0 2px !default\n$content-table-head-cell-color: $text-strong !default\n$content-table-body-last-row-cell-border-bottom-width: 0 !default\n$content-table-foot-cell-border-width: 2px 0 0 !default\n$content-table-foot-cell-color: $text-strong !default\n\n.content\n  @extend %block\n  // Inline\n  li + li\n    margin-top: 0.25em\n  // Block\n  p,\n  dl,\n  ol,\n  ul,\n  blockquote,\n  pre,\n  table\n    &:not(:last-child)\n      margin-bot