cing\n\ndiv.icon-text\n  display: flex\n","@import \"../utilities/mixins\"\n\n$dimensions: 16 24 32 48 64 96 128 !default\n\n.image\n  display: block\n  position: relative\n  img\n    display: block\n    height: auto\n    width: 100%\n    &.is-rounded\n      border-radius: $radius-rounded\n  &.is-fullwidth\n    width: 100%\n  // Ratio\n  &.is-square,\n  &.is-1by1,\n  &.is-5by4,\n  &.is-4by3,\n  &.is-3by2,\n  &.is-5by3,\n  &.is-16by9,\n  &.is-2by1,\n  &.is-3by1,\n  &.is-4by5,\n  &.is-3by4,\n  &.is-2by3,\n  &.is-3by5,\n  &.is-9by16,\n  &.is-1by2,\n  &.is-1by3\n    img,\n    .has-ratio\n      @extend %overlay\n      height: 100%\n      width: 100%\n  &.is-square,\n  &.is-1by1\n    padding-top: 100%\n  &.is-5by4\n    padding-top: 80%\n  &.is-4by3\n    padding-top: 75%\n  &.is-3by2\n    padding-top: 66.6666%\n  &.is-5by3\n    padding-top: 60%\n  &.is-16by9\n    padding-top: 56.25%\n  &.is-2by1\n    padding-top: 50%\n  &.is-3by1\n    padding-top: 33.3333%\n  &.is-4by5\n    padding-top: 125%\n  &.is-3by4\n    padding-top: 133.3333%\n  &.is-2by3\n    padding-top: 150%\n  &.is-3by5\n    padding-top: 166.6666%\n  &.is-9by16\n    padding-top: 177.7777%\n  &.is-1by2\n    padding-top: 200%\n  &.is-1by3\n    padding-top: 300%\n  // Sizes\n  @each $dimension in $dimensions\n    &.is-#{$dimension}x#{$dimension}\n      height: $dimension * 1px\n      width: $dimension * 1px\n","@import \"../utilities/mixins\"\n\n$notification-background-color: $background !default\n$notification-code-background-color: $scheme-main !default\n$notification-radius: $radius !default\n$notification-padding: 1.25rem 2.5rem 1.25rem 1.5rem !default\n$notification-padding-ltr: 1.25rem 2.5rem 1.25rem 1.5rem !default\n$notification-padding-rtl: 1.25rem 1.5rem 1.25rem 2.5rem !default\n\n$notification-colors: $colors !default\n\n.notification\n  @extend %block\n  background-color: $notification-background-color\n  border-radius: $notification-radius\n  position: relative\n  +ltr\n    padding: $notification-paddi