.subtitle\n        color: bulmaRgba($color-invert, 0.9)\n        a:not(.button),\n        strong\n          color: $color-invert\n      .navbar-menu\n        +touch\n          background-color: $color\n      .navbar-item,\n      .navbar-link\n        color: bulmaRgba($color-invert, 0.7)\n      a.navbar-item,\n      .navbar-link\n        &:hover,\n        &.is-active\n          background-color: bulmaDarken($color, 5%)\n          color: $color-invert\n      .tabs\n        a\n          color: $color-invert\n          opacity: 0.9\n          &:hover\n            opacity: 1\n        li\n          &.is-active a\n            color: $color !important\n            opacity: 1\n        &.is-boxed,\n        &.is-toggle\n          a\n            color: $color-invert\n            &:hover\n              background-color: bulmaRgba($scheme-invert, 0.1)\n          li.is-active a\n            &,\n            &:hover\n              background-color: $color-invert\n              border-color: $color-invert\n              color: $color\n      // Modifiers\n      @if type-of($color) == 'color'\n        &.is-bold\n          $gradient-top-left: darken(saturate(adjust-hue($color, -10deg), 10%), 10%)\n          $gradient-bottom-right: lighten(saturate(adjust-hue($color, 10deg), 5%), 5%)\n          background-image: linear-gradient(141deg, $gradient-top-left 0%, $color 71%, $gradient-bottom-right 100%)\n          +mobile\n            .navbar-menu\n              background-image: linear-gradient(141deg, $gradient-top-left 0%, $color 71%, $gradient-bottom-right 100%)\n  // Sizes\n  &.is-small\n    .hero-body\n      padding: $hero-body-padding-small\n  &.is-medium\n    +tablet\n      .hero-body\n        padding: $hero-body-padding-medium\n  &.is-large\n    +tablet\n      .hero-body\n        padding: $hero-body-padding-large\n  &.is-halfheight,\n  &.is-fullheight,\n  &.is-fullheight-with-navbar\n    .hero-body\n      align-items: center\n      display: flex\n      & > .container\n        flex-grow: 1\n        flex-shrink: 1\n  &.is-halfheight\n    min-height: 50vh\n  &.is-fullheight\n    min-height: 100vh\n\n// Components\n\n.hero-video\n  @extend %overlay\n  overflow: hidden\n  video\n    left: 50%\n    min-height: 100%\n    min-width: 100%\n    position: absolute\n    top: 50%\n    transform: translate3d(-50%, -50%, 0)\n  // Modifiers\n  &.is-transparent\n    opacity: 0.3\n  // Responsiveness\n  +mobile\n    display: none\n\n.hero-buttons\n  margin-top: 1.5rem\n  // Responsiveness\n  +mobile\n    .button\n      display: flex\n      &:not(:last-child)\n        margin-bottom: 0.75rem\n  +tablet\n    display: flex\n    justify-content: center\n    .button:not(:last-child)\n      +ltr-property(\"margin\", 1.5rem)\n\n// Containers\n\n.hero-head,\n.hero-foot\n  flex-grow: 0\n  flex-shrink: 0\n\n.hero-body\n  flex-grow: 1\n  flex-shrink: 0\n  padding: $hero-body-padding\n  +tablet\n    padding: $hero-body-padding-tablet\n","@import \"../utilities/mixins\"\n\n$section-padding: 3rem 1.5rem !default\n$section-padding-desktop: 3rem 3rem !default\n$section-padding-medium: 9rem 4.5rem !default\n$section-padding-large: 18rem 6rem !default\n\n.section\n  padding: $section-padding\n  // Responsiveness\n  +desktop\n    padding: $section-padding-desktop\n    // Sizes\n    &.is-medium\n      padding: $section-padding-medium\n    &.is-large\n      padding: $section-padding-large\n","@import \"../utilities/derived-variables\"\n\n$footer-background-color: $scheme-main-bis !default\n$footer-color: false !default\n$footer-padding: 3rem 1.5rem 6rem !default\n\n.footer\n  background-color: $footer-background-color\n  padding: $footer-padding\n  @if $footer-color\n    color: $footer-color\n","$ig-screen-sm-min: 768px !default;\n$ig-screen-xsm-min: 480px !default;\n$ig-white: #fff !default;\n$ig-black: #000 !default;\n$ig-blue: #337ab7 !default;\n$ig-background-black: rgba(0, 0, 0, .4) !default;\n$ig-transparent: rgba(0, 0, 0, 0) !default;\n\n@mixin vendor-prefix($name, $value) {\n  @each $vendor in ('-webkit-', '-moz-', '-ms-', '-o-', '') {\n    #{$vendor}#{$name}: #{$value};\n  }\n}\n\n.image-gallery {\n  @include vendor-prefix('user-select', none);\n  -webkit-tap-highlight-color: $ig-transparent;\n\n  &.fullscreen-modal {\n    background: $ig-black;\n    bottom: 0;\n    height: 100%;\n    left: 0;\n    position: fixed;\n    right: 0;\n    top: 0;\n    width: 100%;\n    z-index: 5;\n\n    .image-gallery-content {\n      top: 50%;\n      transform: translateY(-50%);\n    }\n  }\n\n}\n\n.image-gallery-content {\n  position: relative;\n  line-height: 0;\n  top: 0;\n\n  &.fullscreen {\n    background: $ig-black;\n\n    .image-gallery-slide {\n      background: $ig-black;\n    }\n  }\n}\n\n.image-gallery-slide-wrapper {\n  position: relative;\n\n  &.left,\n  &.right {\n    display: inline-block;\n    width: calc(100% - 113px); // 100px + 8px for border + 5px for padding\n\n    @media (max-width: $ig-screen-sm-min) {\n      width: calc(100% - 84px); // 75px + 6px for border + 3px for padding\n    }\n  }\n  &.image-gallery-rtl {\n    direction: rtl;\n  }\n}\n\n.image-gallery-fullscreen-button,\n.image-gallery-play-button,\n.image-gallery-left-nav,\n.image-gallery-right-nav {\n  appearance: none;\n  background-color: transparent;\n  border: 0;\n  cursor: pointer;\n  outline: none;\n  position: absolute;\n  z-index: 4;\n}\n\n.image-gallery-fullscreen-button,\n.image-gallery-play-button {\n  bottom: 0;\n}\n\n.image-gallery-fullscreen-button {\n  right: 0;\n}\n\n.image-gallery-play-button {\n  left: 0;\n}\n\n.image-gallery-left-nav,\n.image-gallery-right-nav {\n  color: $ig-white;\n  font-size: 5em;\n  padding: 50px 15px;\n  top: 50%;\n  transform: translateY(-50%);\n\n  &[disabled] {\n    cursor: disabled;\n    opacity: .6;\n    pointer-events: none;\n  }\n\n  @media (max-width: $ig-screen-sm-min) {\n    font-size: 3.4em;\n    padding: 20px 15px;\n  }\n\n  @media (max-width: $ig-screen-xsm-min) {\n    font-size: 2.4em;\n    padding: 0 15px;\n  }\n}\n